import React, { useState, useEffect } from 'react'
import app from '../firebaseConfig'
import { getDatabase, ref, get, onValue } from 'firebase/database'
import { Link } from 'react-router-dom'

function Read() {
  const [data, setData] = useState(null)
  const [temperature, setTemperature] = useState(null)
  const [pressure, setPressure] = useState(null)

  const db = getDatabase(app)

  // L automaticamente temperatura e presso ao carregar
  useEffect(() => {
    const tempRef = ref(db, 'test/temperature')
    const pressRef = ref(db, 'test/pressure')

    const unsubscribeTemp = onValue(tempRef, (snapshot) => {
      const value = snapshot.val()
      if (typeof value === 'number') {
        setTemperature(value)
      }
    })

    const unsubscribePress = onValue(pressRef, (snapshot) => {
      const value = snapshot.val()
      if (typeof value === 'number') {
        setPressure(value)
      }
    })

    return () => {
      unsubscribeTemp()
      unsubscribePress()
    }
  }, [db])

  // Busca os demais dados (data/hora e vlvula)
  const fetchData = async () => {
    const dataRef = ref(db, 'test')
    const snapshot = await get(dataRef)
    if (snapshot.exists()) {
      setData(snapshot.val())
      console.log(snapshot.val())
    } else {
      console.log('No data available')
      alert('No data available')
    }
  }

  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      marginTop: '40px',
      fontFamily: 'Arial, sans-serif',
      backgroundColor: 'white',
      minHeight: '100vh',
      paddingBottom: '40px'
    }}>
      {/* Boto Voltar */}
      <Link to="/" style={{ textDecoration: 'none' }}>
        <div style={{
          position: 'fixed',
          top: '20px',
          left: '20px',
          zIndex: 1000
        }}>
          <button
            style={{
              padding: '8px 16px',
              fontSize: '14px',
              backgroundColor: '#ffffff',
              color: '#0053A0',
              border: '2px solid #0053A0',
              borderRadius: '8px',
              cursor: 'pointer',
              boxShadow: '0px 2px 6px rgba(0,0,0,0.1)'
            }}
          >
             Voltar
          </button>
        </div>
      </Link>

      {/* Ttulo e instruo */}
      <h1 style={{ marginBottom: '10px', color: '#0053A0' }}>Leitura da ltima definio de acionamento</h1>
      <p style={{ marginBottom: '20px', fontSize: '16px', color: '#2d3436' }}>
        Clique no boto abaixo para verificar.
      </p>

      {/* Boto Buscar */}
      <button
        onClick={fetchData}
        style={{
          padding: '10px 25px',
          fontSize: '16px',
          backgroundColor: '#FFC700',
          color: '#003B70',
          border: 'none',
          borderRadius: '8px',
          cursor: 'pointer',
          marginBottom: '30px',
          boxShadow: '0px 4px 6px rgba(0,0,0,0.1)',
          fontWeight: 'bold'
        }}
      >
        Buscar ltima definio
      </button>

      {/* Bloco de exibio */}
      <div style={{
        backgroundColor: '#f9f9f9',
        padding: '30px',
        borderRadius: '12px',
        boxShadow: '0px 5px 15px rgba(0, 0, 0, 0.1)',
        width: '320px',
        textAlign: 'left',
        color: '#2d3436'
      }}>
        {/* SEMPRE EXIBIR temperatura e presso */}
        <p><strong>Temperatura:</strong> {temperature !== null ? `${temperature} C` : 'Carregando...'}</p>
        <p><strong>Presso:</strong> {pressure !== null ? `${pressure} hPa` : 'Carregando...'}</p>

        {/* EXIBE APS CLICAR EM "Buscar" */}
        {data && (
          <>
            <p><strong>Data:</strong> 
              {String(data.day).padStart(2, '0')}/
              {String(data.month).padStart(2, '0')}/
              {data.year}
            </p>

            <p><strong>Hora:</strong> 
              {String(data.hour).padStart(2, '0')}:
              {String(data.minute).padStart(2, '0')}:
              {String(data.second).padStart(2, '0')}
            </p>

            <p><strong>Vlvula:</strong> 
              <span style={{ marginLeft: '5px', color: data.control ? '#27ae60' : '#e74c3c' }}>
                {data.control ? 'Ligada' : 'Desligada'}
              </span>
            </p>
          </>
        )}
      </div>
    </div>
  )
}

export default Read
